import React, { useState, useEffect } from 'react';
import arrowRightIcon from '@/assets/arrow-right.svg';
import Checkbox from '@/components/Checkbox';
import { questions, QuestionType, STORAGE_KEYS } from '@/utils/contants';
import Button, { ButtonTheme } from '@/components/button';
import Layout, { LayoutTheme } from '@/components/Layout';
import store from 'store';
import { useHistory, useParams } from '@jupiter/plugin-runtime/router';
import Logo from '@/components/Logo';
import whiteBgIcon from '@/assets/white-bg.svg';
import styles from './index.module.css';

function getCheckInputs(step: number, obj: any) {
  return (questions[step].items as [])
    .filter((item: any) => item.type === QuestionType.Input)
    .reduce((memo: any, item: any) => {
      memo[item.name || item.label] = obj[item.name || item.label] !== '';
      return memo;
    }, {});
}

const Questions = () => {
  const history = useHistory();

  const { currentStep } = useParams<{ currentStep: string }>();
  const [buttonDisabled, setButtonDisabled] = useState(true);

  const step =
    Number(currentStep || 0) < 0 || Number(currentStep || 0) > 7
      ? 0
      : Number(currentStep || 0);

  const [stepValue, setStepValue] = useState<Record<string, unknown>>({});
  const [checkInputs, setCheckInputs] = useState<Record<string, unknown>>({});

  useEffect(() => {
    const key = questions[step].name;

    const currentStepInitialValue = store.get(
      // @ts-expect-error
      STORAGE_KEYS[key],
    );

    if (currentStepInitialValue) {
      setStepValue(currentStepInitialValue);
      setCheckInputs(getCheckInputs(step, currentStepInitialValue));
    } else {
      const ret = (questions[step].items as []).reduce(
        (memo: any, item: any) => {
          memo[item.name || item.label] = '';
          return memo;
        },
        {},
      );
      setStepValue(ret);
      setCheckInputs(getCheckInputs(step, ret));
    }
  }, [step]);

  useEffect(() => {
    let isEmpty = true;
    Object.keys(stepValue).forEach(key => {
      const { type } =
        // @ts-expect-error
        questions[step].items.find(
          (item: any) => item.name === key || item.label === key,
        ) || {};
      if (type && type === QuestionType.Input) {
        if (
          typeof stepValue[key] === 'string' &&
          stepValue[key] !== '' &&
          checkInputs[key]
        ) {
          isEmpty = false;
        }
      } else if (stepValue[key] !== '' && stepValue[key] !== false) {
        isEmpty = false;
      }
    });
    if (isEmpty) {
      setButtonDisabled(true);
    } else {
      setButtonDisabled(false);
    }
  }, [stepValue, checkInputs]);

  const toNextStep = () => {
    // TODO: 检验提示

    if (!buttonDisabled) {
      // @ts-expect-error
      store.set(STORAGE_KEYS[questions[step].name], stepValue);
      // eslint-disable-next-line @typescript-eslint/no-unused-expressions
      step === questions.length - 1
        ? history.push('/join')
        : history.push(`/questions/${step + 1}`);
    }
  };

  const toPrevStep = () => {
    history.push(step < 1 ? `/login` : `/questions/${step - 1}`);
  };

  const handleInputChange = (
    type: QuestionType,
    key: string,
    e: React.BaseSyntheticEvent,
  ) => {
    if (type === QuestionType.Checkbox) {
      if (checkInputs.hasOwnProperty(key)) {
        setCheckInputs((prev: any) => ({
          ...prev,
          [key]: e.target.checked,
        }));
      } else {
        setStepValue((prev: any) => ({
          ...prev,
          [key]: e.target.checked,
        }));
      }
    } else if (e.target.value.trim()) {
      setStepValue((prev: any) => ({
        ...prev,
        [key]: e.target.value,
      }));
    } else {
      setStepValue((prev: any) => ({
        ...prev,
        [key]: '',
      }));
    }
  };

  return (
    <Layout
      progress={`${(100 / 10) * (step + 2)}%`}
      backgroundStyle={{
        background: `url(${whiteBgIcon}) repeat`,
        backgroundSize: 'cover',
      }}>
      <div className={styles.qa}>
        <div className={styles.qaHeader}>
          <div className={styles.processBar}></div>
          <div className={styles.qaHeaderLogo}>
            <Logo theme={LayoutTheme.Light} />
          </div>
        </div>
        <div className={styles.qaMain}>
          <div className={styles.qaMainSide}>
            <span className={styles.qaStepNumber}>{step + 1}.</span>
            <img
              src={arrowRightIcon}
              className={styles.qaSideIcon}
              alt="arrow-right-icon"
            />
          </div>
          <div className={styles.qaMainBody}>
            <p className={styles.qaMainTitle}>
              👨🏻‍ {questions[step].title}(多选)
            </p>
            <div className={styles.qaTable}>
              {questions[step].items?.map(({ name, label, type }) => (
                <Checkbox
                  name={name || label}
                  label={label}
                  key={label}
                  type={type}
                  value={Boolean(
                    type === QuestionType.Checkbox
                      ? stepValue[name || label]
                      : checkInputs[name || label],
                  )}
                  content={
                    (type === QuestionType.Checkbox
                      ? ''
                      : stepValue[name || label]) as string
                  }
                  onCheckboxChange={e =>
                    handleInputChange(QuestionType.Checkbox, name || label, e)
                  }
                  onInputChange={e =>
                    type === QuestionType.Input
                      ? handleInputChange(QuestionType.Input, name || label, e)
                      : undefined
                  }
                />
              ))}
            </div>
            <div className={styles.qaButtonGroup}>
              <Button
                colorTheme={ButtonTheme.Dark}
                height={36}
                onClick={toNextStep}
                disabled={buttonDisabled}>
                下一步
              </Button>
              <Button
                colorTheme={ButtonTheme.Light}
                height={36}
                width={120}
                onClick={toPrevStep}>
                上一步
              </Button>
            </div>
          </div>
        </div>
        <div className={styles.qaButtonGroupSm}>
          <Button
            colorTheme={ButtonTheme.Dark}
            width={327}
            height={32}
            onClick={toNextStep}
            disabled={buttonDisabled}>
            下一步
          </Button>

          <Button
            colorTheme={ButtonTheme.Light}
            width={327}
            height={32}
            onClick={toPrevStep}>
            上一步
          </Button>
        </div>
      </div>
    </Layout>
  );
};

export default Questions;
